body {
  
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin:5% 10%;
  background-color: coral;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.popular-movies{
  display:grid;
  grid-template-columns: repeat(auto-fit,minmax(250px,1fr));
  grid-gap:2rem 1rem;
}
img{
  width:100%;
  height: 40vh;
  object-fit: cover;
  border-radius: 1rem;
  margin-bottom:1rem
}
h2{
  font-size: 0.8rem;
}
.filter-container{
  display: flex;
  font-display: row;
  justify-content: center;
}
.filter-container button{
  margin-right: 2rem;
  min-width: 5rem;
  padding:0.5rem 1rem;
  border:none;
  background: skyblue;
  color:black;
  border-radius: 1rem;
  border: gold;
  font-weight: bold;
  cursor: pointer;
}
button.active{
  background: rgb(65,98,168);
  color:white;
  
}
@media screen and (max-width: 1024px){
  .container{
    width: 80vw;
}
  .filter-container{
    
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
}
  .filter-container button{
      min-width: 0.5rem;
      
  }
}
  

